import { useState, useEffect } from 'react';
import axios from 'axios';

export interface AboutItem {
  id: number;
  title: string;
  description: string;
  image_url: string;
  sort_order: number;
  is_active: boolean;
  created_at: string;
  updated_at: string;
}

interface AboutResponse {
  success: boolean;
  data: AboutItem[];
}

const fallbackData: AboutItem[] = [
  {
    id: 1,
    title: '  ',
    description: '.           .   .',
    image_url: '/placeholder-about-1.png',
    sort_order: 1,
    is_active: true,
    created_at: new Date().toISOString(),
    updated_at: new Date().toISOString()
  },
  {
    id: 2,
    title: '   ()',
    description: '       ,     .',
    image_url: '/placeholder-about-2.png',
    sort_order: 2,
    is_active: true,
    created_at: new Date().toISOString(),
    updated_at: new Date().toISOString()
  },
  {
    id: 3,
    title: '  M105',
    description: '   .  199   20     .     .',
    image_url: '/placeholder-about-3.png',
    sort_order: 3,
    is_active: true,
    created_at: new Date().toISOString(),
    updated_at: new Date().toISOString()
  },
  {
    id: 4,
    title: ' MasterNet',
    description: '  32         .     .',
    image_url: '/placeholder-about-4.png',
    sort_order: 4,
    is_active: true,
    created_at: new Date().toISOString(),
    updated_at: new Date().toISOString()
  }
];

export const useAbout = () => {
  const [aboutItems, setAboutItems] = useState<AboutItem[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchAboutItems = async () => {
      try {
        setLoading(true);
        setError(null);
        
        const response = await axios.get<AboutResponse>('/api/about');
        
        if (response.data.success) {
          setAboutItems(response.data.data);
        } else {
          throw new Error('Failed to fetch about items');
        }
      } catch (err) {
        console.error('Error fetching about items:', err);
        setError(err instanceof Error ? err.message : 'Unknown error');
        // Use fallback data in case of error
        setAboutItems(fallbackData);
      } finally {
        setLoading(false);
      }
    };

    fetchAboutItems();
  }, []);

  return {
    aboutItems,
    loading,
    error
  };
};
